<template>
  <div>
    <div class="sum">搜索总数：{{ books.length }} 本</div>
    <div class="body">
      <div class="book" v-for="(book, index) in books" :key="index">
        <div class="bookleft">
          <img
            v-if="
              book.volumeInfo.imageLinks &&
              book.volumeInfo.imageLinks.smallThumbnail
            "
            :src="book.volumeInfo.imageLinks.smallThumbnail"
            alt="无图片"
            @error="handleImageError"
          />
          <!-- 如果搜索的图书后台没有提供图片地址则用该图片 -->
          <img v-else src="/images/no-cover.jpg" alt="封面缺失" />
        </div>
        <div class="bookmiddle">
          <h3>{{ book.volumeInfo.title | truncate(50) }}</h3>
          <p>
            {{
              book.volumeInfo.authors
                ? book.volumeInfo.authors.join(", ")
                : "未找到作者"
            }}
          </p>
          <div class="booktype">
            <ul>
              <li>
                {{
                  book.volumeInfo.categories
                    ? book.volumeInfo.categories.join(", ")
                    : "未分类"
                }}
              </li>
              <li style="border: 1px solid #f5b62d; color: #f5b62d">
                page:{{ book.volumeInfo.pageCount }}
              </li>
              <li style="border: 1px solid green; color: green">fiction</li>
            </ul>
          </div>
          <div>{{ book.volumeInfo.description | truncate(50) }}</div>
        </div>
        <div class="bookright">
          <div class="star">⭐⭐⭐⭐⭐ &nbsp;</div>

          <div class="view">
            <a :href="book.volumeInfo.canonicalVolumeLink" target="_blank"
              >PREVIEW</a
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      books: [],
    };
  },
  methods: {
    handleImageError(event) {
      event.target.src = "../images/u=3316768721,4044415275&fm=253&gp=0.jpg";
    },
  },
  mounted() {
    this.$bus.$on("getUsers", (users) => {
      this.books = users;
      console.log(this.books);
      //   console.log(users);
    });
  },
  filters: {
    // pd(value) {
    //   return console.log("*" + value);
    // },
  },
};
</script>

<style>
a {
  color: skyblue;
  text-decoration: none;

  cursor: pointer;
}
* {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.sum {
  width: 1200px;
  margin: 0 auto;
  margin-top: 70px;
  /* background-color: pink; */
}

/* body样式开始 */
.body {
  width: 1200px;
  margin: 0 auto;
  /* margin-top: 70px; */
  /* background-color: rgb(255, 0, 43); */
}

.book {
  display: inline-block;
  display: flex;
  margin-top: 10px;
  width: 100%;
  height: 120px;
  border-radius: 10px;
  box-shadow: 0px 1px 1px 0px rgb(147, 147, 147);
  border: 1px solid rgb(183, 183, 183);
  /* background-color: #000000; */
}

/* .book>div {
            display: inline-block;

        } */

.book .bookleft {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10%;
  height: 100%;
  border-radius: 10px;
  /* background-color: #eb7f7f; */
}

.book .bookleft img {
  width: 90px;
  height: 100%;
  border-radius: 5px;
  background-color: #ffffff;
}

.book .bookmiddle {
  width: 70%;
  height: 100%;
  border-radius: 10px;
  padding-left: 15px;
  /* background-color: #eb7f7f; */
}

.book .bookmiddle p {
  margin-bottom: 5px;
}

.bookmiddle .booktype {
  width: 100%;
  /* background-color: rgb(190, 190, 190); */
}

.booktype ul {
  display: flex;
  gap: 15px;
}

.booktype ul li {
  color: skyblue;
  text-align: center;
  line-height: 20px;
  border: 1px solid skyblue;
  border-radius: 10px;
  padding: 2px;
  height: 20px;
  /* background-color: #fff; */
}

.book .bookright {
  width: 20%;
  height: 100%;
  border-radius: 10px;
  /* background-color: #eb7f7f; */
}

.bookright .star {
  margin: 0 auto;
  text-align: center;
  height: 30px;
  /* background-color: pink; */
  margin-top: 15px;
}

.bookright .view {
  color: rgb(12, 179, 246);
  line-height: 30px;
  font-size: 14px;
  text-align: center;
  margin: 0 auto;
  margin-top: 5px;
  width: 120px;
  height: 30px;
  border: 1.5px solid rgb(12, 179, 246);
  /* background-color: pink; */
}
</style>