import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false
//创建全局过滤器truncate，限制显示文字的个数
Vue.filter('truncate', (value, maxLength) => {
  if (!value) return '';
  return value.length > maxLength
    ? value.slice(0, maxLength) + '...'
    : value;
});
new Vue({
  render: h => h(App),
  beforeCreate() {
    Vue.prototype.$bus = this
  }
}).$mount('#app')
