<template>
  <div class="big">
    <div class="child">
      <div class="one">
        <div class="dj" @click="show()">{{ lx }}</div>
        <div class="xz" id="xz" v-show="esshow" @click.stop="check">
          <ul>
            <a>
              <li>类型</li>
            </a>
            <a>
              <li>作者</li>
            </a>
            <a>
              <li>书名</li>
            </a>
          </ul>
        </div>
      </div>
      <div class="two">
        <input
          type="text"
          v-model="userinput"
          placeholder="点击输入搜索内容...."
        />
      </div>
      <div class="three">
        <button @click="search">搜索</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userinput: "",
      arr: [],
      esshow: false,
      lx: "全部",
    };
  },
  mounted() {
    //添加点击页面别处下拉选择框消失
    window.onclick = (e) => {
      if (!e.target.matches(".dj")) {
        this.esshow = false;
      }
    };
  },

  methods: {
    search() {
      // console.log(this.lx);
      if (this.userinput === "") {
        return alert("请输入搜索内容");
      } else {
        if (this.lx === "书名") {
          const xhr = new XMLHttpRequest();
          xhr.open(
            "get",
            `https://www.googleapis.com/books/v1/volumes?q=intitle:${this.userinput}}&maxResults=40`
          );
          xhr.send();
          xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
              // 判断响应状态码为2xx
              if (xhr.status >= 200 && xhr.status <= 300) {
                // 控制台输出响应体
                let zhuan = JSON.parse(xhr.response);
                this.arr = zhuan.items;
                this.$bus.$emit("getUsers", this.arr);
                // console.log(this.arr);
              } else {
                // 输出响应状态码
                console.log(`xhr.status`, xhr.status);
              }
            }
          };
        } else if (this.lx === "作者") {
          const xhr = new XMLHttpRequest();
          xhr.open(
            "get",
            `https://www.googleapis.com/books/v1/volumes?q=inauthor:${this.userinput}}&maxResults=40`
          );
          xhr.send();
          xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
              // 判断响应状态码为2xx
              if (xhr.status >= 200 && xhr.status <= 300) {
                // 控制台输出响应体
                let zhuan = JSON.parse(xhr.response);
                this.arr = zhuan.items;
                this.$bus.$emit("getUsers", this.arr);
                // console.log(this.arr);
              } else {
                // 输出响应状态码
                console.log(`xhr.status`, xhr.status);
              }
            }
          };
        } else if (this.lx === "类型") {
          // subject
          const xhr = new XMLHttpRequest();
          xhr.open(
            "get",
            `https://www.googleapis.com/books/v1/volumes?q=subject:${this.userinput}}&maxResults=40`
          );
          xhr.send();
          xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
              // 判断响应状态码为2xx
              if (xhr.status >= 200 && xhr.status <= 300) {
                // 控制台输出响应体
                let zhuan = JSON.parse(xhr.response);
                this.arr = zhuan.items;
                this.$bus.$emit("getUsers", this.arr);
                // console.log(this.arr);
              } else {
                // 输出响应状态码
                console.log(`xhr.status`, xhr.status);
              }
            }
          };
        } else {
          const xhr = new XMLHttpRequest();
          xhr.open(
            "get",
            `https://www.googleapis.com/books/v1/volumes?q=${this.userinput}}&maxResults=10`
          );
          xhr.send();
          xhr.onreadystatechange = () => {
            if (xhr.readyState === 4) {
              // 判断响应状态码为2xx
              if (xhr.status >= 200 && xhr.status <= 300) {
                // 控制台输出响应体
                let zhuan = JSON.parse(xhr.response);
                this.arr = zhuan.items;
                this.$bus.$emit("getUsers", this.arr);
                // console.log(this.arr);
              } else {
                // 输出响应状态码
                console.log(`xhr.status`, xhr.status);
              }
            }
          };
        }
      }
    },
    show() {
      if (this.esshow != true) {
        this.esshow = true;
      } else {
        this.esshow = false;
      }
    },
    check(e) {
      //  let element = e.traget
      // console.log(e);
      if (e.target.nodeName === "LI") {
        let el = e.target.childNodes[0].data;
        this.lx = el;
      }
    },
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.big {
  position: relative;
  margin: 0 auto;
  width: 100%;
  height: 180px;
  background: url("../images/wallhaven-39qjjd_3840x2160.png");
}

.child {
  margin: 0 auto;
  position: relative;
  top: 150px;
  border-radius: 10px;
  box-shadow: 0px 1px 5px 1px rgb(147, 149, 149);
  width: 800px;
  height: 50px;
  background-color: pink;
}

.child .one {
  float: left;
  width: 12.5%;
  height: 100%;
  border-radius: 10px 0px 0px 10px;
  background-color: #ffffff;
}

.child .one .dj {
  position: relative;
  cursor: pointer;
  text-align: center;
  line-height: 45px;
  width: 90%;
  height: 90%;
  margin-left: 5px;
  margin-top: 2.5px;
  border-radius: 10px;
  background-color: #fff;
}

.child .two {
  float: left;
  width: 75%;
  height: 100%;
  background-color: #ffffff;
}

.child .two input {
  width: 99%;
  height: 80%;
  border: 1px solid rgb(198, 198, 198);
  border-top: 0;
  border-bottom: 0;
  margin-top: 4.5px;
  margin-left: 2px;
  font-size: 20px;
  text-align: center;
}

.child .two input:focus {
  outline: 0;
}

.child .three {
  float: left;
  width: 12.5%;
  height: 100%;
  border-radius: 0px 10px 10px 0px;
  background-color: #ffffff;
}

.child .three button {
  color: #ffffff;
  cursor: pointer;
  width: 90%;
  height: 90%;
  margin-left: 5px;
  margin-top: 2.5px;
  border: 0;
  border-radius: 10px;
  background-color: #3f5cec;
}

.xz {
  /* display: none; */
  position: absolute;
  width: 110px;
  height: 150px;
  top: calc(100% + 1px);
  border-radius: 5px;
  box-shadow: 0px 1px 5px 1px rgb(147, 149, 149);
  background-color: #fff;
}

ul {
  width: 100%;
  height: 100%;
  list-style: none;
}

.xz li {
  text-align: center;
  line-height: 45px;
  width: 50px;
  height: 30%;
  margin: 0 auto;
  margin-top: 2.8px;
  z-index: 100;
  border-bottom: 2px solid rgb(198, 198, 198);
  /* background-color: pink; */
}

a {
  cursor: pointer;
  color: rgb(0, 0, 0);
  text-decoration: none;
}
</style>